@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Heavy.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto Slab';
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-HeavyItalic.woff2") format("woff2");
  font-weight: 900;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #4b5157;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.header {
  background-color: #262D33;
  padding: 15px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 163px;
  height: 40px;
}

.nav {
  color: #fff;
}

.nav__list {
  display: flex;
}

.nav__link {
  font-family: "Lato";
  font-weight: 900;
  font-size: 14px;
  padding: 25px 15px;
}

.search {
  position: relative;
  width: 263px;
  height: 39px;
  background-color: #656B70;
  border-radius: 10px;
  overflow: hidden;
}

.search__input {
  outline: none;
  padding-left: 23px;
  padding-right: 75px;
  padding: 9px 75px 9px 25px;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.search__input::placeholder {
  color: #939699;
  font-size: 14px;
  line-height: 20px;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 71px;
}

.search__button:hover {
  background-color: #4d5155;
}

.search__button-img {
  width: 17.5px;
  height: 18px;
}

.footer {
  min-height: 100vh;
}
